import React, { Component } from 'react';
import './AppointmentCalendar4.css'
import Avatar from '@material-ui/core/Avatar'
import { Button } from '@material-ui/core';

class AppointmentCalendar4 extends Component {
    constructor(props) {
        super(props)
        this.onNext = this.onNext.bind(this)
        this.onPrevious = this.onPrevious.bind(this)
        this.state = {
            showDate: '',
            current: new Date(),
            year: this.props.init_year,
            month: this.props.init_month - 1,
            day: -1,
            a_monthdays: [31, 28, 31, 30, 31, 30, 31, 31, 30, 31, 30, 31],
            a_months: ["JANUARY", "FEBRUARY", "MARCH", "APRIL", "MAY", "JUNE", "JULY", "AUGUST", "SEPTEMBER", "OCTOBER", "NOVEMBER", "DECEMBER"],
            a_weekday: ["Sun", "Mon", "Tue", "Wed", "Thur", "Fri", "Sat"],
            days: [],
            b_selectday: 0,
            drawdays: [],
            available: [],
            clickday: new Date(0, 0, 0),
        }
    }
    componentWillMount() {
        this.makeMonthTable()
    }
    async onSelectday(index) {
        if (this.state.days[index] != null 
            && (this.state.available[this.state.days[index] - 1].first !== false || this.state.available[this.state.days[index] - 1].second !== false)) {
            await this.setState({ clickday: new Date(this.state.year, this.state.month, this.state.days[index]) })
            this.makeMonthTable()
            var startdate = this.state.year + "-" + (this.state.month < 9 ? ('0' + (this.state.month + 1)) : (this.state.month + 1)) + "-" + (this.state.days[index] < 10 ? ('0' + this.state.days[index]) : this.state.days[index])
            this.props.onChange(true, startdate, startdate)
        }
    }

    async makeMonthTable() {
        this.setLeapYear()
        var current = new Date()
        current.setFullYear(this.state.year)
        current.setMonth(this.state.month)
        current.setDate(1)
        await this.setState({
            day: this.state.a_monthdays[this.state.month],
            showDate: String(this.state.year) + " " + String(this.state.a_months[this.state.month]),
            current: current
        })

        var available = []
        for (let i = 0; i < this.state.day; i++)
            available.push({ first: false, second: false })
        for (let i = 0; i < this.props.avail.length; i++) {
            var start = new Date(this.props.avail[i].start_date)
            var end = new Date(this.props.avail[i].end_date)
            while (1) {
                if (start.getFullYear() === this.state.year && start.getMonth() === this.state.month) {
                    if (this.props.avail[i].time_frame === 1) available[start.getDate() - 1].first = true
                    else if (this.props.avail[i].time_frame === 2) available[start.getDate() - 1].second = true
                    else if (this.props.avail[i].time_frame === 3) available[start.getDate() - 1].first = available[start.getDate() - 1].second = true

                    if (start.toISOString().substr(0, 10) === end.toISOString().substr(0, 10)) break
                    start.setDate(start.getDate() + 1)
                }
                else break
            }
        }
        await this.setState({
            available: available
        })

        var startday = this.state.current.getDay()
        var days = []
        for (let i = 0; i < this.state.day; i++)
            days[(i + startday) % 42] = i + 1
        await this.setState({ days: days })
        this.drawMonthTable()
    }
    async drawMonthTable() {
        var drawdays = []
        for (let i = 0; i < 42; i++) {
            var drawday = []
            if (this.state.days[i] != null) {
                if (this.state.available[this.state.days[i] - 1].first === false && this.state.available[this.state.days[i] - 1].second === false) {
                    drawday.push(<svg key={"diagonal" + i} className="diagonal">
                        <line x1="0" y1="100" x2="120" y2="0" className="line" />
                    </svg>)
                } else if (this.state.available[this.state.days[i] - 1].first === false) {
                    drawday.push(
                        <svg key={"diagonal" + i} className="diagonal">
                            <line x1="0" y1="50" x2="120" y2="0" className="line" />
                        </svg>)
                    drawday.push(<div key={"day_first" + i} className="day_second bordertop"></div>)
                } else if (this.state.available[this.state.days[i] - 1].second === false) {
                    drawday.push(<svg key={"diagonal" + i} className="diagonal">
                        <line x1="0" y1="100" x2="120" y2="50" className="line" />
                    </svg>)
                    drawday.push(<div key={"day_second" + i} className="day_second bordertop"></div>)
                }
                for (let j = 0; j < this.props.order.length; j++) {
                    let content = <div>
                        <Avatar className="avatarimage" src={this.props.order[j].avatar_url} />
                        <div className="content">
                            <p>{this.props.order[j].first_name}</p>
                            <p>${Number(this.props.order[j].total).toFixed(2)}</p>
                            <p>{this.props.order[j].team_size}people</p>
                        </div>
                    </div>

                    var book_date = new Date(this.props.order[j].book_date)
                    if (this.state.year === book_date.getFullYear() && this.state.month === book_date.getMonth() && this.state.days[i] === book_date.getDate()) {
                        var startdatetime = new Date(this.props.order[j].start_datetime)
                        if (book_date.toISOString().substr(0, 10) === startdatetime.toISOString().substr(0, 10)) {
                            var gethour = this.props.order[j].start_datetime.substr(11, 2)
                            if (this.props.order[j].order_status === 'D') {
                                if (gethour >= 10 && gethour <= 14) drawday.push(<div key={"day_frist" + i} className="day_first red">{content}</div>)
                                else if (gethour >= 14 && gethour <= 20) drawday.push(<div key={"day_second" + i} className="day_second red">{content}</div>)
                            } else if (this.props.order[j].order_status === 'R') {
                                if (gethour >= 10 && gethour <= 14) drawday.push(<div key={"day_frist" + i} className="day_first orange">{content}</div>)
                                else if (gethour >= 14 && gethour <= 20) drawday.push(<div key={"day_second" + i} className="day_second orange">{content}</div>)
                            } else if (this.props.order[j].order_status === 'P') {
                                if (gethour >= 10 && gethour <= 14) drawday.push(<div key={"day_frist" + i} className="day_first blue">{content}</div>)
                                else if (gethour >= 14 && gethour <= 20) drawday.push(<div key={"day_second" + i} className="day_second blue">{content}</div>)
                            } else if (this.props.order[j].order_status === 'C') {
                                if (gethour >= 10 && gethour <= 14) drawday.push(<div key={"day_frist" + i} className="day_first green">{content}</div>)
                                else if (gethour >= 14 && gethour <= 20) drawday.push(<div key={"day_second" + i} className="day_second green">{content}</div>)
                            }
                        }
                    }
                }
            }
            if (this.state.days[i] != null) {
                var newdate = new Date()
                if (this.state.year === newdate.getFullYear() && this.state.month === newdate.getMonth() && this.state.days[i] === newdate.getDate())
                    drawday.push(<h3 className="bold" key={i}>{this.state.days[i]}</h3>)
                else 
                    drawday.push(<h3 key={i}>{this.state.days[i]}</h3>)
            }

            if (this.state.days[i] != null) {
                if (this.state.year === this.state.clickday.getFullYear() && this.state.month === this.state.clickday.getMonth() && this.state.days[i] === this.state.clickday.getDate()){
                    drawday.push(<div className="clickday" key={"clickday"+this.state.clickday}></div>)
                }
            }

            drawdays.push(<div className="day">{drawday}</div>)
        }
        await this.setState({
            drawdays: drawdays
        })
    }
    async setLeapYear() {
        var c_monthdays = this.state.a_monthdays
        if (this.state.year % 400 === 0 || (this.state.year % 100 !== 0 && this.state.year % 4 === 0))
            c_monthdays[1] = 29
        else
            c_monthdays[1] = 28
        await this.setState({ a_monthdays: c_monthdays })
    }

    async onNext() {
        if (this.state.month === 11) {
            await this.setState({
                year: this.state.year + 1,
                month: 0,
            })
        } else {
            await this.setState({
                month: this.state.month + 1
            })
        }
        this.makeMonthTable()
    }
    async onPrevious() {
        if (this.state.month === 0) {
            await this.setState({
                year: this.state.year - 1,
                month: 11
            })
        } else {
            await this.setState({
                month: this.state.month - 1
            })
        }
        this.makeMonthTable()
    }

    render() {
        return (
            <div className="appointmentcalendar4">
                <div className="showdate">
                    <Avatar className="avatarsize"><Button onClick={this.onPrevious} variant="contained" color="default"><i className="material-icons iconsize">navigate_before</i></Button></Avatar>
                    <h1>{this.state.showDate}</h1>
                    <Avatar className="avatarsize"><Button onClick={this.onNext} variant="contained" color="default"><i className="material-icons iconsize">navigate_next</i></Button></Avatar>
                </div>
                <table className="calendar">
                    <thead>
                        <tr>
                            <th><h1>Sun</h1></th>
                            <th><h1>Mon</h1></th>
                            <th><h1>Tue</h1></th>
                            <th><h1>Wed</h1></th>
                            <th><h1>Thu</h1></th>
                            <th><h1>Fri</h1></th>
                            <th><h1>Sat</h1></th>
                        </tr>
                    </thead>
                    <tbody>
                        <tr>
                            <td onClick={this.onSelectday.bind(this, 0)}>{this.state.drawdays[0]}</td>
                            <td onClick={this.onSelectday.bind(this, 1)}>{this.state.drawdays[1]}</td>
                            <td onClick={this.onSelectday.bind(this, 2)}>{this.state.drawdays[2]}</td>
                            <td onClick={this.onSelectday.bind(this, 3)}>{this.state.drawdays[3]}</td>
                            <td onClick={this.onSelectday.bind(this, 4)}>{this.state.drawdays[4]}</td>
                            <td onClick={this.onSelectday.bind(this, 5)}>{this.state.drawdays[5]}</td>
                            <td onClick={this.onSelectday.bind(this, 6)}>{this.state.drawdays[6]}</td>
                        </tr>
                        <tr>
                            <td onClick={this.onSelectday.bind(this, 7)}>{this.state.drawdays[7]}</td>
                            <td onClick={this.onSelectday.bind(this, 8)}>{this.state.drawdays[8]}</td>
                            <td onClick={this.onSelectday.bind(this, 9)}>{this.state.drawdays[9]}</td>
                            <td onClick={this.onSelectday.bind(this, 10)}>{this.state.drawdays[10]}</td>
                            <td onClick={this.onSelectday.bind(this, 11)}>{this.state.drawdays[11]}</td>
                            <td onClick={this.onSelectday.bind(this, 12)}>{this.state.drawdays[12]}</td>
                            <td onClick={this.onSelectday.bind(this, 13)}>{this.state.drawdays[13]}</td>
                        </tr>
                        <tr>
                            <td onClick={this.onSelectday.bind(this, 14)}>{this.state.drawdays[14]}</td>
                            <td onClick={this.onSelectday.bind(this, 15)}>{this.state.drawdays[15]}</td>
                            <td onClick={this.onSelectday.bind(this, 16)}>{this.state.drawdays[16]}</td>
                            <td onClick={this.onSelectday.bind(this, 17)}>{this.state.drawdays[17]}</td>
                            <td onClick={this.onSelectday.bind(this, 18)}>{this.state.drawdays[18]}</td>
                            <td onClick={this.onSelectday.bind(this, 19)}>{this.state.drawdays[19]}</td>
                            <td onClick={this.onSelectday.bind(this, 20)}>{this.state.drawdays[20]}</td>
                        </tr>
                        <tr>
                            <td onClick={this.onSelectday.bind(this, 21)}>{this.state.drawdays[21]}</td>
                            <td onClick={this.onSelectday.bind(this, 22)}>{this.state.drawdays[22]}</td>
                            <td onClick={this.onSelectday.bind(this, 23)}>{this.state.drawdays[23]}</td>
                            <td onClick={this.onSelectday.bind(this, 24)}>{this.state.drawdays[24]}</td>
                            <td onClick={this.onSelectday.bind(this, 25)}>{this.state.drawdays[25]}</td>
                            <td onClick={this.onSelectday.bind(this, 26)}>{this.state.drawdays[26]}</td>
                            <td onClick={this.onSelectday.bind(this, 27)}>{this.state.drawdays[27]}</td>
                        </tr>
                        <tr>
                            <td onClick={this.onSelectday.bind(this, 28)}>{this.state.drawdays[28]}</td>
                            <td onClick={this.onSelectday.bind(this, 29)}>{this.state.drawdays[29]}</td>
                            <td onClick={this.onSelectday.bind(this, 30)}>{this.state.drawdays[30]}</td>
                            <td onClick={this.onSelectday.bind(this, 31)}>{this.state.drawdays[31]}</td>
                            <td onClick={this.onSelectday.bind(this, 32)}>{this.state.drawdays[32]}</td>
                            <td onClick={this.onSelectday.bind(this, 33)}>{this.state.drawdays[33]}</td>
                            <td onClick={this.onSelectday.bind(this, 34)}>{this.state.drawdays[34]}</td>
                        </tr>
                        <tr>
                            <td onClick={this.onSelectday.bind(this, 35)}>{this.state.drawdays[35]}</td>
                            <td onClick={this.onSelectday.bind(this, 36)}>{this.state.drawdays[36]}</td>
                            <td onClick={this.onSelectday.bind(this, 37)}>{this.state.drawdays[37]}</td>
                            <td onClick={this.onSelectday.bind(this, 38)}>{this.state.drawdays[38]}</td>
                            <td onClick={this.onSelectday.bind(this, 39)}>{this.state.drawdays[39]}</td>
                            <td onClick={this.onSelectday.bind(this, 40)}>{this.state.drawdays[40]}</td>
                            <td onClick={this.onSelectday.bind(this, 41)}>{this.state.drawdays[41]}</td>
                        </tr>
                    </tbody>
                </table>
            </div>
        );
    }

}

export default AppointmentCalendar4;